  * {  margin: 0px; 
  background-color:rgb(247, 247, 247);
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif; 
}

  div {
    padding: 25px;
    
  }

  input, textarea {
    background-color: white;
    }

  img {
    border-radius: 10px;
    margin-top: 20px;
  }

  p {
    margin-top: 40px;
    margin-bottom: 40px;
  }

.form input {
  display: block;
  margin-top: 20px;
  margin-bottom: 20px;
}

button { background-color: white; }

.message { height: 100px; }

h3 { margin: 40px;}

h4 { margin-top: 40px;}

ul {
  display: flex;
  gap: 50px;
  justify-content: center;
  list-style: none;
  text-underline-offset: 10px;
  flex-wrap: wrap;
}

.center {
display: flex;
justify-content: center;
}

.centerPages{
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

a {
  color:rgb(223, 11, 11)
}

.nav-link.active {
  font-weight: bold; /* This will make the active link bold */
  font-size: larger;
}
